* {
    padding: 0px 0px;
    margin: 0px 0px;
}
.nav-bar {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    border-bottom: 1px solid rgb(209, 208, 208);
    width: 100%;
    height: 64px;
    color: rgb(110, 110, 110);
}

.nav-left {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    /* justify-content: center; */
    -webkit-align-items: center;
            align-items: center;
    margin: 0px 20px;
}

.nav-left img {
    margin-left: 20px;
    margin-right: 10px;
    width: 40px;
}

.nav-left h1 {
    font-family: 'PT Sans', sans-serif;
    font-size: 24px;
    font-weight: 200;
}

.nav-btw {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
    left: 7vh;
    margin: 8px 0px;
    border: 0px solid gray;
    width: 85vh;
    height: 48px;
    padding: 0px 20px;
    border-radius: 8px;
    background-color: rgb(238, 237, 237);
}

.nav-btw input {
    width: 82vh;
    background: transparent;
    border: none;
    margin: 0px 10px;
    font-size: 18px;
    outline: none;
}

.nav-right {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: 10;
            flex-wrap: 10;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
    left: 45vh;
    /* gap: 10px; */
}
.menu-left {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 35vh;
    padding: 10px 0px;
    font-family: 'PT Sans', sans-serif;
    color: rgb(116, 116, 116);
}

.menu-list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding: 11px 30px;
}

.menu-list:hover {
    background-color: rgb(228, 228, 228);
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
}

.menu-list p {
    margin: 0px 25px;
    color: black;
    font-size: 17px;
